/**
 * A plugin to run all the axe rules and create generic feedback
 */

let $ = require("jquery");
let Plugin = require("../base");
let annotate = require("../shared/annotate")("everything");
let summary = require("../shared/summary");

let errorTemplate = require("./error.handlebars");

class FixturePlugin extends Plugin {

    constructor(conf) {

        super();

        this.conf = conf;

        this.title = this.conf.branding.brand || "Fixture: " + this.getContext().include[0].join(', ');

        if (typeof this.conf.branding.application === "undefined") {
            var options = this.getOptions();

            this.description = "Running ";

            if (options.runOnly.type === "tag") {
                this.description += "rules for: " + options.runOnly.values.join(', ');

                var inclusions = [];
                var exclusions = [];

                Object.keys(options.rules).forEach((rule) => {
                    if (options.rules.enabled === "true") {
                        inclusions.push(rule);
                    } else {
                        exclusions.push(rule);
                    }
                });

                if (inclusions.length > 0) this.description += ", and also " + inclusions.join(', ');
                if (exclusions.length > 0) this.description += ", except " + exclusions.join(', ');

            }

            if (options.runOnly.type === "rule") this.description += "the following rules: " + options.runOnly.values.join(', ');

            if (options.runOnly.type === "tags") {
                this.description += "rules for: " + option.runOnly.value.include.join(', ');
                if (options.runOnly.value === "exclude") this.description += ", except" + options.runOnly.value.exclude.join(', ');
            }

        } else {
            this.description = this.conf.branding.application;
        }
    }

    getConf() {
        return this.conf;
    }

    getContext() {
        return this.getConf().context;
    }

    getOptions() {
        return this.getConf().options;
    }

    getTitle() {
        return this.title;
    }

    getDescription() {
        return this.description;
    }

    run() {

        let that = this;

        axe.a11yCheck(this.getContext(), this.getOptions(), function (results) {
            if (results.violations.length) {
                $(results.violations).each((j, rule) => {
                    let impact = rule.impact;
                    let help = rule.help;
                    let bestpractice = (rule.tags.indexOf('best-practice') !== -1);
                    $(rule.nodes).each((i, node) => {
                        let $el = $(node.target[0]);

                        let entry = that.error(
                            help, summary(node) + errorTemplate({impact:impact, bestpractice:bestpractice}), $el);

                        annotate.errorLabel(
                            $el, "", help, entry);
                    });
                    that.panel.render();
                });
            }

        });
    }

    cleanup() {
        annotate.removeAll();
    }
}

module.exports = FixturePlugin;



