/**
 * The entry point for tota11y.
 *
 * Builds and mounts the toolbar.
 */

var Toolbar = require('./index.js');

let FixturePlugin = require("./plugins/fixture");

class ToolbarMulti extends Toolbar {

    getParameterByName(name, url) {
        if (!url) url = window.location.href;
        name = name.replace(/[\[\]]/g, "\\$&");
        var regex = new RegExp("[?&]" + name + "(=([^&#]*)|&|#|$)"),
            results = regex.exec(url);
        if (!results) return null;
        if (!results[2]) return '';
        return decodeURIComponent(results[2].replace(/\+/g, " "));
    }

    getConf() {
        return (typeof this.conf === "undefined") ? this.setConf(this.getConf) : this.conf;
    }

    setConf(cb, err) {
        var that = this;
        $.getJSON( this.getParameterByName('conf'), function( data ) {
            that.conf = data;
        }).fail(function(msg){
            console.log("Error loading configuration file: "+msg.responseText);
        }).then(function(){
            typeof cb === 'function' && cb();
        }, function(){
            typeof err === 'function' && err();
        });
    }

    getContext() {
        return this.getConf().context;
    }

    getInclude() {
        return this.getContext().include;
    }

    appendFixture(fixture, pluginsContainer) {
        var conf = this.getConf();
        conf.include = fixture;
        var plugin = new FixturePlugin(conf);
        plugin.appendTo(pluginsContainer);
    }

    appendTo($el) {
        console.log("appendto ");
        var $toolbar = $(toolbarTemplate());
        $el.append($toolbar);

        $toolbar.find(".tota11y-toolbar-toggle").click((e) => {
            e.preventDefault();
            e.stopPropagation();
            $toolbar.toggleClass("tota11y-expanded")
        });

        // Attach each plugin
        var $pluginsContainer = $toolbar.find(".tota11y-plugins");
        plugins.forEach((plugin) => {
            // Mount the plugin to the list
            plugin.appendTo($pluginsContainer);
        });

        if ((this.getParameterByName('fixture') === "true") && this.getParameterByName('conf')) {
            this.setConf(() => {
                this.getInclude().forEach(this.appendFixture(fixture));
            });
        }

    }
}

$(function() {
    console.log("after ToolbarMulti");
    var bar = new ToolbarMulti();

    // TODO: Make this customizable
    bar.appendTo($("body"));
});



